import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { ResultPage } from '../result/result';

import { HTTP } from '@ionic-native/http';


@Component({
  selector: 'page-mysims',
  templateUrl: 'mysims.html',
})
export class MysimsPage {

  cards : any = [];
  me : any = '';
  instr : any = '';
  pl : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private http : HttpClient, public loadingCtrl: LoadingController, public alertCtrl : AlertController, public nhttp : HTTP) {
     this.cards=[];
    this.pl = {
      picdata : '',
      id : 0
    };

  }

  ionViewDidLoad() {
    this.cards=[];
    this.instr='';
    this.me=window.localStorage.getItem('uniq');
    let that=this;
       let url = 'https://mysmilesim.dental/server/mysims.php?me='+window.localStorage.getItem('uniq')+'&end='+Math.random();
        this.http.get(url).subscribe(
         data => {
      	let s = JSON.stringify(data);
                let d = JSON.parse(s);
       that.cards = d.cards;
            that.instr = d.instr;
         },
         err => {
         }
        ); 

  }

 goPic(i,sts)
 { if (sts!=1) this.navCtrl.push( ResultPage, { id : i});
 }


  retake(i)
  { 
  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you - All Done',
      subTitle: 'Your dentist will be in touch shortly',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'btt',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  saveme()
  {
      let that = this;
  }

}



