import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';

import { HTTP } from '@ionic-native/http';

/**
 * Generated class for the NopinPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-nopin',
  templateUrl: 'nopin.html',
})
export class NopinPage {

 public pl : any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public nhttp : HTTP, public loadingCtrl: LoadingController, public alertCtrl : AlertController) {

   this.pl = {
      suburb : '',
      state : '',
      country : '',
      name : '',
      email : '',
      phone : '',
    };
  }

  ionViewDidLoad() {
  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you - All Done',
      subTitle: 'We will notify you with a PIN Code from a local dentist',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'btt',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  saveme()
  {
      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'https://mysmilesim.dental/server/nopin.php';

         this.nhttp.post(url, that.pl, {})
         .then(data => {
            loading.dismiss();
 	    that.doComplete();
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
     

  }
}



