import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { CameraPreview, CameraPreviewPictureOptions, CameraPreviewOptions, CameraPreviewDimensions } from '@ionic-native/camera-preview/ngx';

@Component({
  selector: 'page-preview',
  templateUrl: 'preview.html',
})
export class PreviewPage {

  picture : any = 'assets/img/test.jpg';
  pictureOpts: CameraPreviewPictureOptions = {
    width: 500,
    height: 650,
    quality: 85
  }
  constructor(public navCtrl: NavController, public navParams: NavParams, private cameraPreview: CameraPreview) {

// camera options (Size and location). In the following example, the preview uses the rear camera and display the preview in the back of the webview
 const cameraPreviewOpts: CameraPreviewOptions = {
  x: 0,
  y: 0,
  width: window.screen.width,
  height: window.screen.height,
  camera: 'rear',
  tapPhoto: true,
  previewDrag: true,
  toBack: false,
  alpha: 0.5
}

// start camera
this.cameraPreview.startCamera(cameraPreviewOpts).then(
  (res) => {
    alert('all good')
  },
  (err) => {
    alert('error starting camera')
  });

  }

  ionViewDidLoad() {


  }




// picture options
takepic() {
 alert('snap');

  // take a picture
  this.cameraPreview.takePicture(this.pictureOpts).then((imageData) => {
    this.picture = 'data:image/jpeg;base64,' + imageData;
  }, (err) => {
    alert('error taking picture');
    this.picture = 'assets/img/test.jpg';
  });
}

takesnap()
{
// take a snap shot
   this.cameraPreview.takeSnapshot(this.pictureOpts).then((imageData) => {
     this.picture = 'data:image/jpeg;base64,' + imageData;
   }, (err) => {
     alert('error snapping picture');
     this.picture = 'assets/img/test.jpg';
   });

}



}



