import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { HTTP } from '@ionic-native/http';

import { NopinPage } from '../nopin/nopin';

@Component({
  selector: 'page-dosim',
  templateUrl: 'dosim.html',
})
export class DosimPage {
  public odd : any = 1;
  public title : any = 'PIN Code';
  public blurb : any = 'A';
  public before : any = './assets/img/before0.jpg';
  public after : any = './assets/img/after0.jpg';
  public src : any = './assets/img/before0.jpg';
  public answers : any = [0,0,0,0];
  public timer : any;
  public baf : any = 'BEFORE';
  public comments : any = '';
  public mode : any = 8;
  public pl : any;
  public instructions : any = '...';
  public pinstructions : any = '';
  public pin : any = '';
  public data = [
    {'title' : 'WHITENING', 'blurb' : 'Are you concerned about the <b class="blkh">COLOUR</b> of your teeth?'},
    {'title' : 'VENEERS', 'blurb' : 'Are you concerned about the <b class="blkh">SHAPE</b> of your teeth?'},
    {'title' : 'ORTHODONTICS', 'blurb' : 'Are you concerned about any <b class="blkh">CROOKED</b> teeth?'},
    {'title' : 'COSMETICS', 'blurb' : 'Are you concerned about any <b class="blkh">GAPS</b> in your teeth?'}
  ];

  public current : any = 0;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http : HttpClient, public loadingCtrl: LoadingController, public alertCtrl : AlertController, public nhttp : HTTP) {
    this.pl = {
      uniq : 0,
      ans : '',
      picdata : '',
      comments : '',
      dcode : '',
      suburb : '',
      state : '',
      country : '',
      name : '',
      email : '',
      pushid : '',
      phone : '',
      dcodestat : 0
    };
    this.comments='';
    this.pin='';
    this.mode=8;
  }

 checkpin()
 {   let that=this;
      let url = 'https://mysmilesim.dental/server/checkpin.php?pin='+encodeURIComponent(this.pin);
        this.http.get(url).subscribe(
         data => {
       	       let s = JSON.stringify(data);
                let d = JSON.parse(s);

               if (d.success) {
                that.mode=0;
                that.shownum();
                that.pl.name=d.name;
                that.pl.email =d.email;
                that.pl.dcode = d.dcode;
                that.pl.phone = d.phone;    
               }  
               else {
                 that.pin='Incorrect';
               }
         },
         err => {
         }
        ); 

 }

 
  ionViewDidEnter() {
     this.current=0;
     this.odd=1;
     this.mode=8;
     let that=this;
     this.title='PIN Code';


     that.timer = setInterval(function () {
       that.odd = 1 - that.odd;
       that.src = (that.odd ? that.before : that.after);
       that.baf = (that.odd ? 'BEFORE' : 'AFTER');
     }, 2500);
     this.pl.uniq = window.localStorage.getItem('myac');
       let url = 'https://mysmilesim.dental/server/instructions.php?end='+Math.random();
        this.http.get(url).subscribe(
         data => {
       	       let s = JSON.stringify(data);
                let d = JSON.parse(s);
             that.instructions = d.instructions;
             that.pinstructions = d.pinstructions;
         },
         err => {
         }
        ); 

  }

  gonxt(a)
  { this.answers[this.current]=a;
    if (this.current++>=3) {
       this.takeselfie();
    }
    else 
     this.shownum();
  }

  goback()
  { this.current--;
    this.shownum();
  }

  shownum()
  { this.title = this.data[this.current].title;
    this.blurb = this.data[this.current].blurb;
    this.before = './assets/img/before'+this.current.toString()+'.jpg';
    this.after = './assets/img/after'+this.current.toString()+'.jpg';
    this.src = (this.odd ? this.before : this.after);
   
  }
 
  takeselfie()
  {  this.title='SELFIE TIME!';
     this.mode=1;
 
  }

  pdecode(pwd)
  {
    let l = parseInt(pwd.substr(0,3));
    pwd=pwd.substr(3);

    let d=pwd.length / 12;
    
    let juggleback = [9,4,10,1,8,6,3,2,7,5,0,11];
    let p = '';

    for (let i =0; i< d; i++) {
      let pp=pwd.substr(i*12,12);
      for (let e=0; e<12; e++) 
       p=p + pp.substr(juggleback[e],1);
    }
    return p.substr(0,l);
  }

   takepicture()
  { 
 
  }



  nocode()
  { this.pl.dcodestat = 1;
  }

  yescode()
  { this.pl.dcodestat = 0;
  }

  nopin()
  { this.navCtrl.push( NopinPage );
  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you - All Done',
      subTitle: 'Track your photos in the See My Simulations tab below',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'btt',
	  role: 'cancel',
          handler: () => {
             this.navCtrl.pop();
          }
        }
      ]
    });
    alert.present();
  }

  saveme()
  {
    
  }

}




